/* =====================================================================================
Fonts */
@font-face {
  font-family: "SFProDisplay";
  src: url("../fonts/SFProDisplay-Regular.woff2") format("woff2"), url("../fonts/SFProDisplay-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "SFProDisplay";
  src: url("../fonts/SFProDisplay-Medium.woff2") format("woff2"), url("../fonts/SFProDisplay-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "SFProDisplay";
  src: url("../fonts/SFProDisplay-Semibold.woff2") format("woff2"), url("../fonts/SFProDisplay-Semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "SFProDisplay";
  src: url("../fonts/SFProDisplay-Bold.woff2") format("woff2"), url("../fonts/SFProDisplay-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

/* =====================================================================================
Variables */
/* =====================================================================================
Basic styles */
body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "SFProDisplay", "Helvetica", sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 24px;
  color: #2f3035; }

img {
  max-width: 100%;
  height: auto; }

a {
  color: inherit;
  text-decoration: none; }

ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0; }

* {
  box-sizing: border-box; }

/* =====================================================================================
Container */
.container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding-right: 15px;
  padding-left: 15px; }

/* =====================================================================================
Visually-hidden */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

/* =====================================================================================
Header */
.header__top {
  font-weight: 700; }

.header__top-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 25px;
  padding-bottom: 21px; }

.header__logo {
  outline: none; }

.header__logo svg {
  fill: #2f3035; }

.header__logo:hover svg,
.header__logo:focus svg {
  fill: #1c62cd; }

.header__logo:active svg {
  fill: rgba(28, 98, 205, 0.7); }

.header__box {
  display: flex; }

.header__box-address {
  position: relative;
  width: 265px;
  margin-right: 60px;
  padding-left: 30px; }

.header__box-address::before {
  content: "";
  position: absolute;
  top: -10px;
  left: 0;
  width: 30px;
  height: 30px;
  background-image: url("../img/user-icon/icon-address.svg"); }

.header__bottom {
  margin-bottom: 78px; }

.header__bottom-inner {
  padding-top: 15px;
  padding-bottom: 13px;
  background-color: #f0f0f4; }

/* =====================================================================================
Menu */
.menu__list {
  display: flex;
  align-items: center; }

.menu__list-item + .menu__list-item {
  margin-left: 50px; }

.menu__list-link {
  outline: none; }

.menu__list-link:hover,
.menu__list-link:focus {
  color: #1c62cd; }

.menu__list-link:active {
  color: rgba(28, 98, 205, 0.7); }

/* =====================================================================================
User-list */
.user-list {
  display: flex;
  align-items: center;
  margin-right: 20px; }

.user-list__item + .user-list__item {
  margin-left: 10px; }

.user-list__link {
  outline: none; }

.user-list__link svg {
  fill: #2f3035; }

.user-list__link:hover svg,
.user-list__link:focus svg {
  fill: #1c62cd; }

.user-list__link:active svg {
  fill: rgba(28, 98, 205, 0.7); }

.user-list__link--basket {
  position: relative; }

.user-list__link--basket .user-list__basket-num {
  position: absolute;
  bottom: 2px;
  right: -5px;
  width: 20px;
  height: 20px;
  font-size: 10px;
  font-weight: 500;
  line-height: 20px;
  text-align: center;
  color: #ffffff;
  background-color: #1c62cd;
  border-radius: 50%; }

/* =====================================================================================
Menu-categories */
.menu-categories__list {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: center; }

.menu-categories__list-link {
  position: relative;
  outline: none;
  transition: all linear 0.3s; }

.menu-categories__list-link:hover,
.menu-categories__list-link:focus {
  color: #000000; }

.menu-categories__list-link::before {
  content: "";
  position: absolute;
  right: 0;
  bottom: -14px;
  left: auto;
  width: 0;
  height: 3px;
  background-color: #1c62cd;
  transition: all linear 0.3s; }

.menu-categories__list-link:hover::before,
.menu-categories__list-link:focus::before {
  right: auto;
  left: 0;
  width: 100%; }
